body {
  @include typography-body;
  background: $color-backgroundgray;

  margin: 0px;
  padding: 0px;
}

.MetaDash {
  section {
    margin-top: 50px;
  }

  .prose{
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 50px;
    font-family: $font-serif;
    font-size: 24px;
    text-align: center;
    color: $color-darkgray;
  
    .stat {
        color: $color-fortunoff;
    }
  }
  

  section.headings-area{
    margin-top: 34px;
  }

  .headings-area {
    background-color: white;

    .TagFilter {
      .top-area {
        background-color: $color-headingsbackground;
        height: 80px;
        padding: 0px 80px;
        display: flex;
        box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.16);
      }

      .title-area {
        font-size: 24px;
        display: flex;
        padding-right: 20px;
        flex: auto auto;
        align-items: center;
        justify-content: flex-end;
      }

      .TextInput {
        padding: 0px;
        display: flex;
        flex: 2 2;

        align-items: center;

        input {
          height: 42px;
        }
      }

      .TagPool {
        height: 85px;
        overflow: hidden;
        overflow-y: scroll;
        margin: 30px 80px;

        .TagItem{
          font-family: $font-sans;
          color: $color-darkgray;
          font-size: 14px;
          margin: 0px;
          padding: 0.25em 0.5em;
          border: none;
          display: flex;
          justify-content: center;
          align-items: center;
        }

        .TagItem:hover{
          color: $color-teal;
          background-color: $color-lightmist;
        }
      }

      .SelectionPool {
        
        flex: 4 4;
        padding: 19px 30px;
        overflow-y: scroll;
        overflow-x: hidden;
        overflow: scroll;
        overflow-y: hidden;
        white-space: nowrap;
        max-height: 80px;

        .SelectionPoolItem:hover {
          color: unset;
          background-color: $color-lightmist;
          .button {
            box-shadow: none;
          }
        }
        .SelectionPoolItem {
          display: flex;

          // display: inline-block;
          background-color: white;
          border-radius: 10px;
          height: 20px;
          overflow: hidden;
          box-sizing: border-box;
          justify-content: center;
          align-items: center;
          padding-left: 1em;
          font-family: $font-sans;
          color: $color-textgray;

          .button {
            border: none;
            color: $color-teal;
          }

          .button:hover {
            box-shadow: none;
          }
        }
      }
    }
  }
}
